@import 'common.css';

.results {
  display: flex;
}

.result {
  display: flex;
  flex-direction: column;
  width: 50%;
  padding: 20px;
}

.results > div:nth-child(2) {
  background-color: var(--secondary-color);
}

.result > div {
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
}

.result > p {
  text-align: center;
  margin-bottom: 0;
}

.label {
  font-size: 1.8em;
  font-weight: bold;
  color: var(--light-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.important {
  font-size: 3.5em;
  font-weight: bolder;
  margin-left: 15px;
  color: var(--dark-color);
}

.chart-wrapper {
  display: flex;
  flex-direction: column;
}

.chart-wrapper .label {
  text-align: center;
  margin-bottom: 20px;
}

.chart-row {
  display: flex;
  justify-content: flex-end;
}

.chart-col {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
}

.chart-col span {
  margin: 0;
}

.social {
  text-align: center;
  color: var(--primary-color);
  font-size: 2em;
  font-weight: bolder;
  display: flex;
  margin-top: 30px;
}

.social .text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.social-buttons {
  display: flex;
}

.social-icon-wrapper {
  padding: 5px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--primary-color);
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-left: 10px;
}

.social-icon {
  color: white;
}

@media (max-width: 700px) {
  .results {
    display: block;
    width: 100%;
  }
  .result {
    width: 100%;
    padding: 20px 0;
  }

  .label {
    font-size: 1em;
  }

  .important {
    font-size: 2em;
  }

  .social {
    font-size: 1.3em;
    flex-direction: column;
  }

  .social-buttons {
    margin: 5px 0;
    justify-content: center;
  }

  .social-icon-wrapper {
    width: 30px;
    height: 30px;
  }
}
